import React, { useEffect, useState } from "react";
import { Card } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { DataTable, PageHeader } from "../../../components";
import { decodeQueryData, getSortType, logger } from "../../../utils";
import { regionsServices } from "../../../services";

function Regions() {
  const location = useLocation();
  const { search } = location;
  const [regionData, setRegionData] = useState([]);
  const [param, setParam] = useState({});
  const [page, setPage] = useState(1);
  const [sizePerPage, setSizePerView] = useState(10);
  const [searchName, setSearchName] = useState("");
  const [defaultSort, setDefaultSort] = useState([
    {
      dataField: "",
      order: "",
    },
  ]);
  const [tableLoader, setTableLoader] = useState(false);
  const [noOfPage, setNoOfPage] = useState();
  const [totalCount, setTotalCount] = useState(0);
  //   const [searchName, setSearchName] = useState("");

  useEffect(() => {
    if (search) {
      const data = decodeQueryData(search);
      setParam(data);
      setPage(data?.page ?? 1);
      if (data?.sortType) {
        const sortData = [
          {
            order: getSortType(data?.sortType),
            dataField: data?.sortBy,
          },
        ];
        setDefaultSort(sortData);
      } else {
        setDefaultSort({
          dataField: "",
          order: "",
        });
      }
    }
  }, [location]);

  const getRegionData = async () => {
    setTableLoader(true);
    try {
      let queryParams = {
        offset: (page - 1) * sizePerPage,
        limit: sizePerPage,
        sortBy: param?.sortBy,
        sortType: param?.sortType,
        name: searchName,
      };
      const res = await regionsServices.getRegionsList(queryParams);
      const { success, data } = res;
      if (success) {
        setRegionData(data.rows);
        setNoOfPage(data.count > 0 ? Math.ceil(data.count / sizePerPage) : 1);
        setTotalCount(data.count);
      }
    } catch (error) {
      logger(error);
    }
    setTableLoader(false);
  };

  //   const tableReset = () => {
  //     setTableLoader(true);
  //     setHubData([]);
  //     setNoOfPage(0);
  //     setTotalCount(0);
  //   };

  //   const headerSortingClasses = (column, sortOrder) => {
  //     return sortOrder === "asc" ? "sorting_asc" : "sorting_desc";
  //   };

  //   const onSortColumn = (field, order) => {
  //     const data = { ...param };
  //     data.sortBy = field;
  //     data.sortType = order === "asc" ? "ASC" : "DESC";
  //     navigateWithParam(data, navigate, pathname);
  //     tableReset();
  //   };
  const columns = [
    {
      dataField: "name",
      text: "Regions",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
    },
    {
      dataField: "city",
      text: "City",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
    },
    {
      dataField: "visibleOn",
      text: "Visible On",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
    },
    {
      dataField: "action",
      text: "Action",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
    },
  ];

  const tableReset = () => {
    setTableLoader(true);
    setRegionData([]);
    setNoOfPage(0);
    setTotalCount(0);
  };

  const getSearchValue = (val) => {
    setSearchName(val);
    if (val) {
      tableReset();
    }
  };

  useEffect(() => {
    getRegionData();
  }, [param]);

  return (
    <div className="nk-block-head nk-block-head-sm">
      <div className="nk-block-between">
        <Card className="w-100 rounded-2 mb-4">
          <Card.Body>
            <PageHeader heading="Regions" />
          </Card.Body>
        </Card>
      </div>

      <DataTable
        sizePerPagination={false}
        hasLimit
        noOfPage={noOfPage}
        sizePerPage={sizePerPage}
        page={page}
        count={totalCount}
        tableData={regionData}
        tableColumns={columns}
        // param={param}
        defaultSort={defaultSort}
        tableLoader={tableLoader}
        setSizePerPage={setSizePerView}
        tableReset={tableReset}
        getSearchValue={getSearchValue}
        searchPlaceholder="Regions"
      />
    </div>
  );
}

export default Regions;
